import LC from 'leancloud-storage';  // 导入 leancloud 数据存储模块
import { invalid } from 'moment';

class AccountService {
    /**
     * 注册帐号
     * @param {*} username 
     * @param {*} password 
     * @param {*} email 
     * @returns 
     */
    signUp(username, password, email = '') {
        let user = new LC.User();
        user.setUsername(username);
        user.setPassword(password);
        // 注册时, 用户昵称 与 用户名相同
        user.set('nickname', username);
        if (email) {
            user.setEmail(email);
        }
        return new Promise((resolve, reject) => {
            user.signUp().then(user => {
                // 
                console.log(`新用户注册成功，objectId: ${user.id}`);
                resolve(user);
            }).catch((error) => {
                console.log('signUp():', error.code);
                reject(error);
            });

        });
    }

    /**
     * 用户登录
     * @param {*} username 
     * @param {*} password 
     */
    logIn(username, password) {
        return new Promise((resolve, reject) => {
            LC.User.logIn(username, password).then(user => {
                // 
                LC.Cloud
                resolve(this.fromJson(user));
            }).catch(error => {
                // 
                console.log('logIn(): ', error)
                reject(error);
            });

        });
    }

    /**
     * 注册, 退出登录
     */
    logOut() {
        return new Promise((resolve, reject) => {
            LC.User.logOut().then(resp => {
                // 
                resolve(LC.User.current());
            }).catch(error => {
                // 
                reject('logOut()', error);
            });

        });
    }

    // 用户是否登录, 这个可以用下面的 currentUser() 代替
    // 因为不知道这个函数是否已经有使用, 所以还是留着吧.
    isLoggedIn() {
        let currentUser = LC.User.current(); // 获取当前用户
        if (currentUser) {
            return currentUser;    // 已有登录用户，返回当前用户
        }
        return false;     // 没有用户登录，返回false
    };

    // 获取当前已登录的用户, 本地数据不需要 Promise
    currentUser() {
        // return new Promise((resolve, reject) => {
        var user = LC.User.current();
        if (user) {
            var currUser = this.fromJson(user);
            console.log('current user: ', currUser);
            // resolve(currUser);
            return currUser;
        }
        return null;
        // reject(user);
        // });
    }

    // 更新用户头像
    updateAvatar(image) {
        return new Promise((resolve, reject) => {
            var user = LC.User.current();  // 获取当前用户
            let name = image.name;  // 获取图像文件名
            let img = new LC.File(name, image);   // 创建文件对象
            // 保存文件(上传到服务器)
            img.save()
                .then(file => {
                    console.log('update file: ', file);
                    // 设置用户 avatar 字段的值
                    user.set('avatar', file.get('url'));
                    user.save().then(u => {
                        // 更新当前用户的数据
                        LC.User.current().change();
                        resolve(this.fromJson(u));
                    });
                })
                .catch(error => {
                    reject(error);
                });
        });
    }


    // 将从服务器读取的一个用户数据转为对象格式, 以方便调用
    fromJson(user) {
        return {
            id: user.id,
            username: user.get('username'),
            email: user.get('email'),
            emailVerified: user.get('emailVerified'),
            mobilePhoneNumber: user.get('mobilePhoneNumber'),
            mobilePhoneVerified: user.get('mobilePhoneVerified'),
            nickname: user.get('nickname'),
            avatar: user.get('avatar') ,
        };
    }

}

export default new AccountService();