<template>
  <div class="col-lg-8" style="width: 500px">
    <v-row>
      <v-col class="d-flex justify-center align-center mb-6">
        <strong class="text-h3">Log In</strong>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          label="Email or Username"
          prepend-inner-icon="mdi-account"
          v-model="username"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          label="password"
          :prepend-inner-icon="password ? 'mdi-lock-open' : 'mdi-lock'"
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showPassword ? 'text' : 'password'"
          @click:append="showPassword = !showPassword"
          v-model="password"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-btn block color="success" @click="login"> Log in </v-btn>
      </v-col>
    </v-row>
     <v-dialog v-model="dialog" width="500">
      <!-- <template v-slot:activator="{ on, attrs }">
        <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">
          Click Me
        </v-btn>
      </template> -->

      <v-card>
        <v-card-title class="headline red darken-4" primary-title>
          <span class="white--text">登录失败</span>
        </v-card-title>

        <v-card-text class="py-4">
          {{ errorInfo }}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> 确定 </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AccountService from "@/service/account_service";
export default {
  name: "account-login", // AccountLogin

  data: () => ({
    showPassword: false, // 用于是否显示密码
    username: "zhangsan", // 用于输入用户名或Email
    password: "123456", // 用于输入密码
    dialog: false, // 是否显示对话框
    errorInfo: "", // 错误信息
    message: { "210": "用户名或密码不正确", "211": "用户名不存在", "-1": "无法连接到服务器" },
  }),
  methods: {
    login() {
      AccountService.logIn(this.username, this.password)
        .then(user=>{
          const curr = localStorage.getItem("preRoute");
          this.$store.commit('SET_CURRENT_USER', user);
          if (curr == null || curr =='/') {
            // 没有的话，或是从 / 登录的话，就跳到仪表盘
            this.$router.push({ path: "/dashboard" });
          } else {
            // 跳转到进入登录页之前的页面
            this.$router.push({ path: curr });
          }
        })
        .catch((error) => {
          this.dialog = true;
          this.errorInfo = this.message[error.code];
          console.log("登录失败: ", error.code);
        });
    },
  },
};
</script>

